@mixin border-radius($radius) {
	-webkit-border-radius: $radius;
	-moz-border-radius: $radius;
	-ms-border-radius: $radius;
	border-radius: $radius; }


@mixin box-shadow($box-shadow) {
	-moz-box-shadow: $box-shadow;
	-webkit-box-shadow: $box-shadow;
	box-shadow: $box-shadow; }


@mixin background-clip($bg-clip) {
	-webkit-background-clip: $bg-clip;
	-moz-background-clip: $bg-clip;
	background-clip: $bg-clip; }

@mixin linear-gradient ($l-grad) {
	-webkit-linear-gradient: $l-grad;
	-o-linear-gradient: $l-grad;
	linear-gradient: $l-grad; }




@mixin linear-gradient($angle, $color-stops...) {
  background: -webkit-linear-gradient($angle, $color-stops);
  background: -moz-linear-gradient($angle, $color-stops);
  background: -o-linear-gradient($angle, $color-stops);
  background: linear-gradient($angle, $color-stops); }



@mixin placeholder() {
  input::-webkit-input-placeholder {
    @content; }
  input:-moz-placeholder {
    @content; }
  input::-moz-placeholder {
    @content; }
  input:-ms-input-placeholder {
    @content; } }

@mixin flex {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex; }

@mixin transition($time) {
	transition: $time ease-out;
	-webkit-transition:  $time ease-out;
	-moz-transition:  $time ease-out;
	-ms-transition:  $time ease-out;
	-o-transition:  $time ease-out; }

@mixin transition-ease($time) {
	transition: $time ease;
	-webkit-transition:  $time ease;
	-moz-transition:  $time ease;
	-ms-transition:  $time ease;
	-o-transition:  $time ease; }

@mixin transition-text-shadow($time) {
	transition:text-shadow $time ease {}
	-webkit-transition: text-shadow $time ease;
	-moz-transition: text-shadow $time ease;
	-ms-transition: text-shadow $time ease;
	-o-transition: text-shadow $time ease; }

@mixin transform($do) {
	-moz-transform: $do;
	-ms-transform: $do;
	-webkit-transform: $do;
	-o-transform: $do;
	transform: $do; }


@mixin rotate ($deg) {
	@include transform(rotate(#{$deg}deg)); }


@mixin scale($scale) {
	@include transform(scale($scale)); }


@mixin translate ($x, $y) {
	@include transform(translate($x, $y)); }


@mixin skew ($x, $y) {
	@include transform(skew(#{$x}deg, #{$y}deg)); }

